import React from "react";

import { ToWords } from "to-words";
import { Name } from "name-df-generator";
import { genders } from "culture-generator";

import "./AppDescription.css";
import AppTitle from "./AppTitle";

const months = [
  "granite",
  "slate",
  "felsite",
  "hematite",
  "malachite",
  "galena",
  "limestone",
  "sandstone",
  "timber",
  "moonstone",
  "opal",
  "obsidian"
];

function AppDescription(props) {
  const { onRefresh, adventurer } = props;

  const parentsDOM = React.useMemo(() => {
    const father = new Name();
    const motherName = new Name();
    const pronoun = adventurer._gender === genders.MALE ? "he is" : "she is";

    const son = adventurer._gender === genders.MALE ? "son" : "daughter";
    return (
      <span>
        {pronoun} the {son} of {father.toString()} and {motherName.toString()}
      </span>
    );
  }, [adventurer]);

  const childrenDOM = React.useMemo(() => {
    if (Math.random() > 0.7) return;

    const pronoun = adventurer._gender === genders.MALE ? "he is" : "she is";

    const spouse = new Name();

    const children = Array(Math.ceil(Math.random() * 3) + 2)
      .fill()
      .map(() => {
        return new Name({ seed: Math.random() }).toString();
      });

    return (
      <span>
        {pronoun} married to {spouse.toString()} and has {children.length}{" "}
        children: {children.slice(0, -1).join(", ")} and {children.slice(-1)[0]}
      </span>
    );
  }, [adventurer]);

  const familyDOM = React.useMemo(() => {
    const dom = [];

    if (childrenDOM != null) dom.push(childrenDOM, ". ");
    dom.push(parentsDOM);

    return <div className="Text--Family">{dom}</div>;
  }, [parentsDOM, childrenDOM]);

  const badBodyAttributes = React.useMemo(() => {
    return adventurer._bodyAttributes.bad;
  }, [adventurer]);

  const goodBodyAttributes = React.useMemo(() => {
    return adventurer._bodyAttributes.good;
  }, [adventurer]);

  const badSoulAttributes = React.useMemo(() => {
    return adventurer._soulAttributes.bad;
  }, [adventurer]);

  const goodSoulAttributes = React.useMemo(() => {
    return adventurer._soulAttributes.good;
  }, [adventurer]);

  const bodyAttributesDOM = React.useMemo(() => {
    const pronoun = adventurer._gender === genders.MALE ? "he is" : "she is";

    const badString =
      badBodyAttributes.length === 0
        ? null
        : badBodyAttributes.length === 1
        ? badBodyAttributes[0]
        : [
            badBodyAttributes.slice(0, -1).join(", "),
            "and",
            badBodyAttributes.slice(-1)[0]
          ].join(" ");

    const goodString =
      goodBodyAttributes.length === 0
        ? null
        : goodBodyAttributes.length === 1
        ? goodBodyAttributes[0]
        : [
            goodBodyAttributes.slice(0, -1).join(", "),
            "and",
            goodBodyAttributes.slice(-1)[0]
          ].join(" ");

    if (badString == null && goodString == null) return null;

    const dom = [];

    if (goodString != null) {
      dom.push(
        <span className="Text--Good">
          {pronoun} {goodString}
        </span>
      );
    }

    if (badString != null) {
      if (goodString != null) {
        dom.push(<span className="Text--Bad"> but </span>);
      }

      dom.push(
        <span className="Text--Bad">
          {pronoun} {badString}
        </span>
      );
    }

    return <div>{dom}</div>;
  }, [adventurer, badBodyAttributes, goodBodyAttributes]);

  const soulAttributesDOM = React.useMemo(() => {
    const pronoun = adventurer._gender === genders.MALE ? "he has" : "she has";

    const badString =
      badSoulAttributes.length === 0
        ? null
        : badSoulAttributes.length === 1
        ? badSoulAttributes[0]
        : [
            badSoulAttributes.slice(0, -1).join(", "),
            "and",
            badSoulAttributes.slice(-1)[0]
          ].join(" ");

    const goodString =
      goodSoulAttributes.length === 0
        ? null
        : goodSoulAttributes.length === 1
        ? goodSoulAttributes[0]
        : [
            goodSoulAttributes.slice(0, -1).join(", "),
            "and",
            goodSoulAttributes.slice(-1)[0]
          ].join(" ");

    if (badString == null && goodString == null) return null;

    const dom = [];

    if (goodString != null) {
      dom.push(
        <span className="Text--Good">
          {pronoun} {goodString}
        </span>
      );
    }

    if (badString != null) {
      if (goodString != null) {
        dom.push(<span className="Text--Bad"> but </span>);
      }

      dom.push(
        <span className="Text--Bad">
          {pronoun} {badString}
        </span>
      );
    }

    return <div>{dom}</div>;
  }, [adventurer, badSoulAttributes, goodSoulAttributes]);

  const dob = React.useMemo(() => {
    const toWord = new ToWords();

    const pronoun = adventurer.gender === genders.MALE ? "he" : "she";

    const age = toWord
      .convert(Math.floor(Math.random() * (99 - 30) + 70))
      .toLowerCase();

    const date = Math.ceil(Math.random() * (30 - 1) + 1);

    const year = Math.floor(Math.random() * (700 - 30) + 70);

    const month = months[Math.floor(Math.random() * months.length)];

    return (
      <div className="Text--DOB">
        {pronoun} is {age} years old, born on the {date} of {month} in year{" "}
        {year}
      </div>
    );
  }, [adventurer]);
  return (
    <div className="AppDescription">
      <AppTitle name={adventurer.fullName} />

      <div className="AppDescriptionSection">
        {familyDOM}
        {dob}
        <div>{adventurer.appearance}</div>
        {bodyAttributesDOM}
        {soulAttributesDOM}
        <div>{adventurer.facets}</div>
      </div>

      <AppTitle showFPS={false} name={`Culture: ${adventurer.culture.name}`} />

      <div className="AppDescriptionSection">
        <div>{adventurer.ethics}</div>
        <div>{adventurer.beliefs}</div>
      </div>

      <div>
        <center>
          <button onClick={onRefresh}>Randomize</button>
          <br />
          <span className="Text--Caption">or hit R</span>
        </center>
      </div>
    </div>
  );
}

export default React.memo(AppDescription);
