import React from "react";
import { Culture } from "culture-generator";

import AppDescription from "./AppDescription";

import "./App.css";

export default function App() {
  const [culture, setCulture] = React.useState(new Culture());
  const [adventurer, setAdventurer] = React.useState();

  const onClick = React.useCallback(() => {
    setCulture(new Culture());
  }, []);

  React.useEffect(() => {
    setAdventurer(culture.createAdventurer());
  }, [culture]);

  React.useEffect(() => {
    const listener = (ev) => {
      if (ev.key !== "r") return;
      onClick();
    };

    window.addEventListener("keydown", listener);

    return () => window.removeEventListener("keydown", onClick);
  }, [onClick]);

  return adventurer == null ? null : (
    <div className="App">
      <AppDescription adventurer={adventurer} onRefresh={onClick} />
      <footer class="Text--Caption">
        Created with ❤️ By{" "}
        <a
          href="http://www.lucatabone.com"
          target="_blank"
          rel="noreferrer"
          title="Creator"
        >
          me ^^ /
        </a>
      </footer>
    </div>
  );
}
