import React from "react";

import "./AppTitle.css";

function AppTitle(props) {
  const { name, showFPS = true } = props;

  const [fps, setFPS] = React.useState(100);

  React.useEffect(() => {
    const intervalID = window.setInterval(() => {
      setFPS(Math.floor(Math.random() * (99 - 70) + 70));
    }, 1000);

    return () => window.clearInterval(intervalID);
  }, []);

  return (
    <div className="AppTitle">
      {showFPS === true && <div className="AppTitleFPS">FPS: {fps}</div>}
      <div className="AppTitleName">{name}</div>
    </div>
  );
}

export default React.memo(AppTitle);
